import { Component, inject } from '@angular/core';
import { SearchService } from '../../search/search.service';

interface Case {
  name: string;
  checked: boolean;
}

@Component({
  selector: 'add-case',
  standalone: false,
  templateUrl: './add-case.component.html',
  styleUrl: './add-case.component.scss'
})
export class AddCaseComponent {
  private readonly searchService = inject(SearchService);

  searchText: string = '';
  fromDate: Date | undefined;
  toDate: Date | undefined;

  minDate: Date | undefined;
  maxDate: Date = new Date();

  caseList: Case[] = [
    { name: 'Evidence Car Accident Case', checked: false },
    { name: 'Evidence Bike Theft', checked: false },
    { name: 'Evidence Parking lot robbery', checked: false },
    { name: 'Evidence Theft Case', checked: false }
  ];
  filteredCaseList: Case[] = this.caseList;

  constructor() {}

  get canAddCase(): boolean {
    return this.caseList.filter((caseItem) => caseItem.checked).length > 0;
  }

  get numberOfSelected(): number {
    return this.searchService.selectedSessions().length;
  }

  onDateChange(event: Event) {
    console.log(event);
  }

  searchCase() {
    this.filteredCaseList = this.caseList.filter((caseItem) =>
      caseItem.name.toLowerCase().includes(this.searchText.toLowerCase())
    );
  }

  onSelectCase(selectedCase: Case) {
    const caseItem = this.caseList.find((caseItem) => caseItem.name === selectedCase.name);

    if (caseItem) {
      caseItem.checked = !caseItem.checked;
    }
  }

  onAddCase() {}
}
