import { computed, Injectable, signal } from '@angular/core';

export interface SearchFormData {
  freeText: string;
  date: string;
  organization: string;
  user: string; // Account
  mediaCategory: string[]; // Category
  sessionFilter: string[];
  mediaSource: string;
  eventId: string;
  classification: string;
  eventNumber: string;
  caseNumber: string;
  victimPOI: string;
  offence: string;
  bwvEvent: string[];
  inCarVideo: string[];
  sessionLocation: string[];
}

export interface DateRange {
  start: Date | undefined;
  end: Date | undefined;
}

@Injectable({ providedIn: 'root' })
export class SearchService {
  private $initialData = signal<SearchFormData>({
    freeText: '',
    date: '',
    organization: '',
    user: '',
    mediaCategory: [],
    sessionFilter: [],
    mediaSource: 'Body worn camera',
    eventId: '',
    classification: '',
    eventNumber: '',
    caseNumber: '',
    victimPOI: '',
    offence: '',
    bwvEvent: [],
    inCarVideo: [],
    sessionLocation: []
  });

  private $searchFormData = signal<SearchFormData>({
    freeText: '',
    date: '',
    organization: '',
    user: '',
    mediaCategory: [],
    sessionFilter: [],
    mediaSource: 'Body worn camera',
    eventId: '',
    classification: '',
    eventNumber: '',
    caseNumber: '',
    victimPOI: '',
    offence: '',
    bwvEvent: [],
    inCarVideo: [],
    sessionLocation: []
  });

  private $dateRange = signal<DateRange>({
    start: undefined,
    end: undefined
  });

  private $selectedSessions = signal<string[]>([]);

  constructor() {}

  get initialData() {
    return this.$initialData.asReadonly();
  }

  get formData() {
    return this.$searchFormData.asReadonly();
  }

  get dateRange() {
    return this.$dateRange.asReadonly();
  }

  get isUnchanged() {
    const unchanged = computed(() => {
      const initial = this.$initialData();
      const current = this.$searchFormData();

      return JSON.stringify(initial) === JSON.stringify(current);
    });

    return unchanged;
  }

  get selectedSessions() {
    return this.$selectedSessions.asReadonly();
  }

  updateFormData(data: Partial<SearchFormData>) {
    this.$searchFormData.update((prev) => {
      return { ...prev, ...data };
    });
  }

  updateDateRange(data: Partial<DateRange>) {
    this.$dateRange.update((prev) => {
      return { ...prev, ...data };
    });
  }

  updateSelectedSessions(data: string, action: 'add' | 'remove') {
    if (action === 'add') {
      this.$selectedSessions.update((prev) => {
        return [...prev, data];
      });
    } else {
      this.$selectedSessions.update((prev) => {
        return prev.filter((val) => val !== data);
      });
    }
  }

  resetForm(form: string) {
    if (form === 'basic') {
      this.updateFormData({
        freeText: '',
        date: '',
        organization: '',
        user: '',
        mediaCategory: []
      });
    } else if (form === 'advanced') {
      this.updateFormData({
        sessionFilter: [],
        mediaSource: 'Body worn camera',
        eventId: '',
        classification: '',
        eventNumber: '',
        caseNumber: '',
        victimPOI: '',
        offence: '',
        bwvEvent: [],
        inCarVideo: [],
        sessionLocation: []
      });
    }

    this.$dateRange.set({
      start: new Date(),
      end: new Date()
    });
  }
}
